#error .Panel__in {
    height: calc(100vh - 52px);
}

#error .main {
    margin: 0;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#error .logo {
    width: 80px;
    height: 80px;
    margin-top: -20px;
}

#error .logo svg {
    width: 80px;
    height: 80px;
}

#error .logo-space {
    height: 21%;
}

#error .picture {
    width: 220px;
    height: 220px;
    flex-shrink: 0;
    flex-grow: 0;
    margin-top: -80px;
    background: url('data:image/png;base64,'
    ) center center no-repeat;
    background-size: contain;
}

#error .text-1 {
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    color: var(--text_primary);
}

#error .text-2 {
    font-size: 15px;
    font-weight: normal;
    text-align: center;
    color: var(--text_primary);
    margin-top: 6px;
}

#error .button-wrap {
    margin-top: 20px;
}